
.projectAreaContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding-left: 30px; */
}

.projectsHeading {
    /* color: rgba(255, 255, 255, 0.649); */
    font-size: 100px;
    font-family: 'WhitneyBold';
    /* position: absolute; */
    z-index: -10;
    /* top: 60px;
    left: 80px; */
    background: -webkit-linear-gradient(#eee, rgba(0, 0, 0, 0));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.projectArea {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: .3s;
}

.projectAreaBlurredAndScaledDown {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: .3s;
    filter: blur(12px);
    -webkit-filter: blur(12px);
    transform: scale(.9);
}

.projectAreaScrolledUp {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: .3s;
    transform: scale(.9) translateY(-1050px);
}

.projectAreaScrolledUpForDown {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    /* transition: .3s; */
    transform: scale(.9) translateY(-1050px);
}

.projectAreaScrolledDown {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: .3s;
    transform: scale(.9) translateY(+1050px);
}

.projectAreaDowned {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    /* transition: .3s; */
    transform: scale(.9) translateY(+1050px);
}

.projectAreaDownedAnimated {
    background-color: #131315;
    height: 580px;
    width: 100%;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 2px 2px 15px 0px #00000044;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 20px;
    margin-bottom: 20px;
    transition: .3s;
    transform: scale(.9) translateY(+1050px);
}

.projectImageArea {
    height: 70%;
    width: 100%;
    background-color: #683bda;
    background-position: top;
    object-fit: cover;
    overflow: hidden;
}

.projectImage {
    max-width: 100%;
    display: block;
}

.projectDetailsArea {
    flex: 1;
    padding: 25px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.projectDetailsAreaLeft {

}

.projectDetailsAreaRight {
    height: 100%;
}

.projectDetailGithubBtn {
    width: 115px;
    /* background-color: #eee; */
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 7px;
    /* justify-content: center; */
    text-decoration: none;
    transition: .2s;
}

.projectDetailGithubBtn:hover {
    background-color: #19191c;
    cursor: pointer;
}

.projectDetailGithubBtnText {
    color: white;
    font-family: 'WhitneySemiBold';
    font-size: 15px;
    margin-left: 10px;
    margin: 0;
    margin-bottom: 2px;
    margin-left: 12px;
}

.projectName {
    margin: 0;
    color: white;
    font-family: 'WhitneySemiBold';
    font-size: 30px;
}

.projectAbout {
    color: white;
    font-family: 'WhitneyRegular';
}

.projectTechUsed {
    color: white;
    font-family: 'WhitneySemiBold';
}

.projectTechUsedList {
    color: white;
    font-family: 'WhitneyRegular';
    margin-left: 7px;
}

.projectNavigationBtn {
    height: 85px;
    width: 85px;
    background-color: #131315;
    border-radius: 100px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s;
}

.projectNavigationBtnInvisible {
    height: 85px;
    width: 85px;
    background-color: #131315;
    border-radius: 100px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s;
    transform: scale(0);
}

.projectNavigationBtn:hover {
    box-shadow: 0px 0px 15px 0px #00000044;
}

.projectNavigationBtn:active {
    transform: scale(0.8);
}

.projectNavigationBtnTop {
    opacity: 0;
    height: 85px;
    width: 85px;
    background-color: #131315;
    border-radius: 100px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigationArrow {
    margin-top: 7px;
    margin-left: 2px;
}

.navigations {
    display: flex;
    flex-direction: row;
    transition: 0.2s;
    margin-bottom: 10px;
}

.projectNavigationBtnUp {
    height: 85px;
    width: 85px;
    background-color: #131315;
    border-radius: 100px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s;
}

.projectNavigationBtnUpInvisible {
    height: 85px;
    width: 0px;
    background-color: #131315;
    border-radius: 100px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s;
    transform: scale(0);
}

.projectNavigationBtnUp:hover {
    box-shadow: 0px 0px 15px 0px #00000044;
}

.projectNavigationBtnUp:active {
    transform: scale(0.8);
}

.navigationArrowUp {
    margin-bottom: 7px;
    margin-right: 2px;
    transform: rotate(180deg);
}

.navigationsMobile {
    display: none;
    flex-direction: row;
    transition: 0.2s;
    margin-bottom: 30px;
}

@media (max-width: 992px) {
    .projectsHeading {
        position: absolute !important;
        top: 610px !important;
        left: 20px !important;

    }
    .projectNavigationBtnTop {
        display: none;
    }

    .projectArea {
        margin-left: 0;
        margin-top: 70px;
        /* height: auto; */
        width: 82%;
        height: 375px;
    }

    .navigations {
        display: none;
    }

    .projectDetailsArea {
        flex-direction: column;
        align-items: flex-start;
        /* height: 100px; */
    }

    .projectDetailsAreaRight {
        display: flex;
        flex-direction: row;
    }

    .projectDetailGithubBtn {
        padding: 10px 0;
        width: auto;
        margin-right: 25px;
    }

    .projectImageArea {
        height: 180px;
    }

    .projectImage {
        max-width: auto;
        height: 100%;
    }

    .projectName {
        font-size: 20px;
    }

    .projectAbout {
        font-size: 13px;
    }

    .projectTechUsed {
        font-size: 15px;
    }

    .projectDetailGithubBtnText {
        font-size: 15px;
    }

    .projectNavigationBtnUp {
        margin: 0;
    }

    .projectAreaBlurredAndScaledDown {
        margin-left: 0;
        margin-top: 70px;
        height: 375px;
        width: 82%;
    }

    .projectAreaScrolledUp {
        margin-left: 0;
        margin-top: 70px;
        height: 375px;
        width: 82%;
    }

    .projectAreaScrolledUpForDown {
        margin-left: 0;
        margin-top: 70px;
        height: 375px;
        width: 82%;
    }

    .projectAreaScrolledDown {
        margin-left: 0;
        margin-top: 70px;
        height: 375px;
        width: 82%;
    }

    .projectAreaDowned {
        margin-left: 0;
        margin-top: 70px;
        height: 375px;
        width: 82%;
    }

    .projectAreaDownedAnimated {
        margin-left: 0;
        margin-top: 70px;
        height: 375px;
        width: 82%;
    }

    .navigationsMobile {
        display: flex;
    }
}