
.discordCard {
  /* height: 600px; */
  width: 300px;
  background-color: #131315;
  border-radius: 10px;
  display: flex;
  position: relative;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 2px 2px 15px 0px #00000044;
  padding-bottom: 5px;
  margin-left: auto;
  margin-right: 30px;
}

.discordCardHeader {
  height: 120px;
  width: 100%;
  background-color: #683bda;
  /* -webkit-filter: grayscale(100%);
  filter: grayscale(100%); */
  opacity: .7;
}

.discordCardProfileArea {
  height: 85px;
  width: 85px;
  border-radius: 100px;
  background-color: #131315;
  position: absolute;
  top: 70px;
  left: 15px;
  overflow: hidden;
  padding: 5px;
}

.discordCardProfileImage {
  height: 100%;
  width: 100%;
  border-radius: 100px;
  overflow: hidden;
}

.profileImage {
  width: 100%;
  height: 100%;
  background-color: antiquewhite;
  object-fit: cover;
}

.discordCardDetailArea {
  flex: 1;
  /* background-color: bisque; */
  padding: 20px 15px;
}

.bannerName {
  color: white;
  font-size: 20px;
  margin-top: 40px;
  font-family: 'WhitneyBold';
  margin-bottom: 15px;
}

.bannerNameTag {
  color: #babbbe;
}

.bannerDivider {
  background-color: #33353b;
  height: 1px;
  width: 100%;
  margin-top: 10px;
}

.bannerSectionTitle {
  color: #e5e7eb;
  text-transform: uppercase;
  font-weight: bold;
  font-family: 'WhitneyBold';
  margin-top: 18px;
  font-size: 12px;
  margin-bottom: 0;
}

.bannerAbout {
  margin-top: 10px;
  font-family: 'WhitneyRegular';
  font-size: 13px;
  color: #e5e7eb;
  line-height: 20px;
}

.connectionsBtn {
  width: auto;
  height: 50px;
  border-radius: 5px;
  border: 1px solid #24262a;
  margin-top: 15px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.githubSVG {
  fill: white;
}

.githubHandle {
  font-family: 'WhitneySemiBold';
  color: white;
  margin-left: 10px;
}

.discordVerifiedSVG {
  margin-left: 5px;
}

.githubRedirect {
  transform: rotate(45deg);
  margin-left: auto;
}

.connectionBtnLeft {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media (max-width: 992px) {
  .discordCard {
    margin: auto;
  }
}